<script>
  import { API_URL } from '../config.js';
  import { onMount, onDestroy } from 'svelte';
  import { Html5QrcodeScanner } from "html5-qrcode"; // Asegrate de tener instalada la librera: npm install html5-qrcode

  let scanner; // Referencia al escner
  let qrCodeMessage = '';
  let colorAsistencia = "";
  let ultimoEstudiante = "";

  async function consultarEstudiante(numDocumento) {
    try {
            const token = localStorage.getItem('token');
            const payload = {
            numDocumento: numDocumento
            };
            const response = await fetch(`${API_URL}/registrarAsistencia`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'token': token ? `${token}` : ''
                },
                body: JSON.stringify(payload)
            });

            if (response.status == 200) {
                const data = await response.json();
                qrCodeMessage = `${data.message}`;
                colorAsistencia = "green"
            } else if (response.status == 404) {
                const errorData = await response.json();
                qrCodeMessage = `${errorData.message}`;
                colorAsistencia = "red";
            } else if (response.status == 401) {
                const errorData = await response.json();
                qrCodeMessage = `${errorData.message}`;
                colorAsistencia = "yellow";
            }
        } catch (error) {
            console.error('Error en la peticin de asistencia:', error);
            alert('Error al conectar con el servidor. Intenta de nuevo ms tarde.');
        }
  }

  // Funcin para manejar el xito de la lectura del QR
  async function onScanSuccess(qrCodeValue) {
    if (ultimoEstudiante !== qrCodeValue) {
        console.log('Cdigo QR escaneado:', qrCodeValue);        
        consultarEstudiante(qrCodeValue)
        ultimoEstudiante = qrCodeValue
    }
  }
  onMount(() => {
      // Configuracin del escner QR
      scanner = new Html5QrcodeScanner(
          "qr-reader", // ID del contenedor donde se mostrar el escner
          { 
              fps: 10, // Frames por segundo
              qrbox: { width: 250, height: 250 } // Tamao del rea de escaneo
          }, 
          false // No abrir el selector de cmara automticamente
      );

      // Iniciar el escaneo con la funcin de xito 
      scanner.render(onScanSuccess);
  });

  onDestroy(() => {
      // Limpiar el escner cuando se destruya el componente
      if (scanner) {
          scanner.clear().catch(error => console.error('Error al limpiar el escner:', error));
      }
  });
</script>

<style>
  .qr-scanner-container {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin: 20px;
  }

  #qr-reader {
      width: 100%;
      max-width: 400px;
  }

  #qr-reader__dashboard_section {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }

  .qr-scanner-container button {
      margin-top: 20px;
      padding: 10px 20px;
      background-color: #007bff;
      color: white;
      border: none;
      cursor: pointer;
  }

  .qr-scanner-container button:hover {
      background-color: #0056b3;
  }
</style>

<div class="qr-scanner-container">
  Nmero de documento: <input id="numdoc"><button on:click={() => consultarEstudiante(document.getElementById("numdoc").value)}>Consultar Estudiante</button>
  <div id="qr-reader"></div> <!-- Aqu se muestra el escner QR -->
  {#if qrCodeMessage}
      <p style="color: {colorAsistencia};">{qrCodeMessage}</p>
  {/if}
</div>